@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


#components-layout-demo-fixed .logo {
  width: 120px;
  height: 31px;
  background: rgba(255,255,255,.2);
  margin: 16px 24px 16px 0;
  float: left;
}


/* estilos de home */



.img-trans{
  background-image: url('https://res.cloudinary.com/theledzed/image/upload/v1539502751/capsula.png');
  width: 100%;
  height: 100%;
  background-size: cover;
  position:absolute ;
  display: flex;
  flex-direction: column;
  
 
  
  
}


.img-trans:before{
  content: '';
  width: 100%;
  height: 100%;
  background-color: black;
  background-size: 100% 100%;
  position: absolute;
  opacity: 0;
  
  
  
}

.img-trans h1{
  position: absolute;
  color: white ;
  z-index: 1;
  font-size: 60px;
  font-weight: bold;
  top:25%;
  left: 5%;
  
  
}
.img-trans p{
  
  position: absolute;
  color: white ;
  z-index: 1;
  font-size: 20px;
  font-weight: bold;
  top:35%;
  left: 5%;
  
  
}





.img-trans2{
